import React from "react";
import LogoSrc from "../../assets/far.gif";
import { styled } from "../../../../stitches.config";

export function NavBar(props) {
  return (
    <>
      <Nav>
        <img src={LogoSrc} width="70px" height="70px" />
      </Nav>
    </>
  );
}

const Nav = styled("div", {
  height: "100%",
  padding:'10px 30px',
  background: "#1e1f1c",
  display: "flex",
});
