import { styled } from "../../../stitches.config";

export const Selection = styled("div", {
  fontFamily: "Arial",
  background: "#d3d3d3",
  minHeight: "100vh",
  padding: "20px",
  p: { margin: 0 },
});

export const SubmitButton = styled("button", {
  backgroundColor: "#444444",
  color: "#ffffff",
  padding: "10px 15px",
  width: "520px",
  borderRadius: "3px",
  marginTop: "30px",
  fontFamily: "Arial",
  cursor: "pointer",
  fontSize: "18px",
  transitionDuration: "0.4s",
  verticalAlign: "middle",
  "&:hover": {
    color: "white",
    backgroundColor: "#727070",
  },
});

export const SelectButton = styled("button", {
  backgroundColor: "white",
  color: "#d82b31",
  border: "2px solid #d82b31",
  width: "160px",
  height: "50px",
  marginRight: "25px",
  fontFamily: "Arial",
  marginTop: "0",
  cursor: "pointer",
  fontSize: "14px",
  transitionDuration: "0.4s",
  verticalAlign: "middle",
  "&:hover": {
    color: "white",
    backgroundColor: "#d82b31",
  },
  variants: {
    selected: {
      true: {
        color: "white",
        backgroundColor: "#d82b31",
      },
    },
  },
});

export const MainWrapper = styled("div", {
  fontFamily: "Roboto",
  background: "#d3d3d3",
  minHeight: "100vh",
  padding: "10px",
  p: { margin: 0 },
});

export const FooterContainer = styled("div", {
  height: "100%",
  padding: "10px 30px",
  background: "#1e1f1c",

  p: {
    color: "white",
    textAlign: "center",
    fontSize: "18px",
  },
});
