import React, { useState } from "react";
import {
  Footer,
  FooterContainer,
  MainWrapper,
  SelectButton,
  SubmitButton,
} from "./main.styles";
import { NavBar } from "../components/navBar/navBar";
import CarSrc from "../assets/car.png";
import FMirrorSrc from "../assets/FrontMirror.png";
import BonnetSrc from "../assets/Bonnet.png";
import { Slider } from "@material-ui/core";
import Typography from "@mui/material/Typography";
import { makeStyles } from "@material-ui/styles";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";
import { fibreMaterials, manufacturingProcess, resinMaterials } from "./data";
import { useRef } from "react";

const useStyles = makeStyles({
  root: {
    color: "#1e1f1c", // Your custom color here
  },
});

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const colors = ["#4caf50", "#607d8b", "#795548", "#ff9800"];

const labelsConst = ["Option 1", "Option 2", "Option 3", "Option 4"];

const defaultCo2Data = {
  labels: labelsConst,
  datasets: [
    {
      label: "CO2 Data",
      data: [],
      backgroundColor: colors,
    },
  ],
};

const defaultEnergyData = {
  labels: labelsConst,
  datasets: [
    {
      label: "Energy Data",
      data: [],
      backgroundColor: colors,
    },
  ],
};

const options = {
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
    title: {
      display: true,
      text: "CO2 Data",
    },
  },
};

const optionsEnergy = {
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
    title: {
      display: true,
      text: "Energy Data",
    },
  },
};

export function CarModelPage(props) {
  const classes = useStyles();
  const scrollRef = useRef(null);
  const [selectSrc, setSelectedSrc] = useState(BonnetSrc);
  const [selectPart, setSelectedPart] = useState("BONNET");
  const [selectedFibre, setSelectedFibre] = useState(0);
  const [selectedResin, setSelectedResin] = useState(0);
  const [selectedProcess, setSelectedProcess] = useState(0);
  const [submittedOptions, setSubmittedOptions] = useState([]);
  const [rwvalue, setRWValue] = useState(50);
  const [chartData, setChartData] = useState(defaultCo2Data);

  const [energyData, setEnergyData] = useState(defaultEnergyData);

  const changeRWValue = (event, value) => {
    setRWValue(value);
  };
  const getText = (valu) => `${rwvalue}%`;

  let cor = "";
  const onImageClick = (e) => {
    // cor = cor + `,${e.clientX - 20},${e.clientY - 130}`;
    // console.log(cor);
    //
  };

  const onWindowClick = (e) => {
    setSelectedSrc(FMirrorSrc);
    setSelectedPart("MIRROR");
  };

  const onBonnetClick = (e) => {
    setSelectedSrc(BonnetSrc);
    setSelectedPart("BONNET");
  };

  const onsubmitClick = () => {
    const perKgCo2 =
      fibreMaterials[selectedFibre].co2 * (1 - rwvalue / 100) +
      resinMaterials[selectedResin].co2 * (rwvalue / 100) +
      manufacturingProcess[selectedProcess].co2;
    const co2 = parseFloat(
      (perKgCo2 * fibreMaterials[selectedFibre].weight).toFixed(2)
    );

    const perKgEnergy =
      fibreMaterials[selectedFibre].energy * (1 - rwvalue / 100) +
      resinMaterials[selectedResin].energy * (rwvalue / 100) +
      manufacturingProcess[selectedProcess].energy;
    const energy = parseFloat(
      (perKgEnergy * fibreMaterials[selectedFibre].weight).toFixed(2)
    );

    const data = {
      totalCO2: co2,
      fibre: fibreMaterials[selectedFibre].name,
      resin: resinMaterials[selectedResin].name,
      process: manufacturingProcess[selectedProcess].name,
      rw: rwvalue,
      totalEnergy: energy,
    };

    let weightMultiplyFactor = 3.5;

    let tempData = [...submittedOptions];
    if (tempData.length > 3) tempData.shift();
    tempData.push({
      totalCO2: data.totalCO2,
      fibre: data.fibre,
      resin: data.resin,
      process: data.process,
      RW: data.rw,
      totalEnergy: data.totalEnergy,
    });
    setSubmittedOptions(tempData);

    const tempLabels = labelsConst.slice(0, tempData.length);

    const tempCO2ChartData = {
      labels: tempLabels,
      datasets: [
        {
          label: "CO2 Data",
          data: tempData.map((d) => weightMultiplyFactor * d.totalCO2),
          backgroundColor: colors.slice(0, tempData.length),
        },
      ],
    };

    const tempEnergyChartData = {
      labels: tempLabels,
      datasets: [
        {
          label: "Energy Data",
          data: tempData.map((d) => weightMultiplyFactor * d.totalEnergy),
          backgroundColor: colors.slice(0, tempData.length),
        },
      ],
    };
    console.log(tempEnergyChartData);
    setEnergyData(tempEnergyChartData);
    setChartData(tempCO2ChartData);
    scrollRef.current.scrollIntoView({ behavior: "smooth" });

  };

  return (
    <>
      <NavBar></NavBar>
      <MainWrapper>
        <div
          className="container"
          style={{
            padding: "0px 0px ",
            marginTop: "10px",
            fontFamily: "Arial",
          }}
        >
          <div className="row">
            <div
              className="col-xl-6"
              style={
                {
                  // border: "solid",
                  // borderWidth: "0.01rem",
                  // borderColor: "black",
                }
              }
            >
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  position: "relative",
                }}
              >
                <img
                  onClick={onImageClick}
                  src={CarSrc}
                  useMap="#workmap"
                  style={{
                    width: "600px",
                    position: "relative",
                  }}
                />
                <img
                  src={selectSrc}
                  usemap="#workmap2"
                  style={{
                    width: "600px",
                    position: "absolute",
                  }}
                />
                <map name="workmap">
                  <area
                    shape="poly"
                    coords="184,104,200,98,224,91,251,83,284,75,305,71,323,67,336,65,350,70,365,82,383,96,403,112,414,123,417,131,410,141,381,154,337,168,301,180,272,186,248,191,237,186,230,183,223,175,212,163,198,147,192,133,185,122,180,112"
                    alt="window"
                    onClick={onWindowClick}
                  />
                  <area
                    shape="poly"
                    coords="242,220,256,215,291,206,327,195,367,183,402,170,432,154,454,143,458,140,466,144,478,152,491,162,506,176,518,188,526,200,518,214,506,226,466,252,436,264,395,276,373,282,343,284,318,283,296,275,278,265,255,251,238,243,237,234,238,228,241,221"
                    alt="bonnet"
                    onClick={onBonnetClick}
                  />
                </map>
                <map name="workmap2">
                  <area
                    shape="poly"
                    coords="184,104,200,98,224,91,251,83,284,75,305,71,323,67,336,65,350,70,365,82,383,96,403,112,414,123,417,131,410,141,381,154,337,168,301,180,272,186,248,191,237,186,230,183,223,175,212,163,198,147,192,133,185,122,180,112"
                    alt="window"
                    onClick={onWindowClick}
                  />
                  <area
                    shape="poly"
                    coords="242,220,256,215,291,206,327,195,367,183,402,170,432,154,454,143,458,140,466,144,478,152,491,162,506,176,518,188,526,200,518,214,506,226,466,252,436,264,395,276,373,282,343,284,318,283,296,275,278,265,255,251,238,243,237,234,238,228,241,221"
                    alt="bonnet"
                    onClick={onBonnetClick}
                  />
                </map>
              </div>
            </div>
            <div
              className="col-lg-6"
              style={{
                position: "relative",
                marginTop: "30px",
                // border: "1px solid",
              }}
            >
              <h5 style={{ textAlign: "start" }}>
                PLEASE SELECT MATERIALS FOR - {selectPart}
              </h5>
              <>
                <h6 style={{ marginTop: "20px" }}>FIBRE</h6>
                <SelectButton
                  selected={selectedFibre === 0}
                  onClick={() => setSelectedFibre(0)}
                >
                  FLAX FIBRE
                </SelectButton>
                <SelectButton
                  selected={selectedFibre === 1}
                  onClick={() => setSelectedFibre(1)}
                >
                  GLASS FIBRE
                </SelectButton>
                <SelectButton
                  selected={selectedFibre === 2}
                  onClick={() => setSelectedFibre(2)}
                >
                  CARBON FIBRE
                </SelectButton>
              </>
              <>
                <h6 style={{ marginTop: "20px" }}>RESIN</h6>
                <SelectButton
                  selected={selectedResin === 0}
                  onClick={() => setSelectedResin(0)}
                >
                  EPOXY
                </SelectButton>
                <SelectButton
                  selected={selectedResin === 1}
                  onClick={() => setSelectedResin(1)}
                >
                  Biobased Epoxy
                </SelectButton>
                <SelectButton
                  selected={selectedResin === 2}
                  onClick={() => setSelectedResin(2)}
                >
                  PFA
                </SelectButton>
              </>

              <>
                <h6 style={{ marginTop: "20px" }}>Manufacturing Process</h6>
                <SelectButton
                  selected={selectedProcess === 0}
                  onClick={() => setSelectedProcess(0)}
                >
                  Resin Infusion
                </SelectButton>
                <SelectButton
                  selected={selectedProcess === 1}
                  onClick={() => setSelectedProcess(1)}
                >
                  Carbon Fibre/Epoxy Prepreg
                </SelectButton>
                <SelectButton
                  selected={selectedProcess === 2}
                  onClick={() => setSelectedProcess(2)}
                >
                  SMC Compression Moulding/Press
                </SelectButton>
              </>

              <div style={{ marginTop: "40px" }}>
                <Typography id="non-linear-slider" gutterBottom>
                  RW: {rwvalue}%
                </Typography>
                <Slider
                  classes={{
                    root: classes.root,
                  }}
                  size="small"
                  style={{ width: 525 }}
                  min={0}
                  max={100}
                  step={1}
                  value={rwvalue}
                  onChange={changeRWValue}
                  valueLabelDisplay="on"
                  getAriaValueText={getText}
                  valueLabelFormat={getText}
                  aria-labelledby="discrete-slider-restrict"
                  // marks={marks}
                  aria-label="Small"
                  // color="secondary"
                />
                {rwvalue}
              </div>

              <SubmitButton onClick={onsubmitClick}>
                COMPARE AND FIND SUITABLE DESIGN
              </SubmitButton>
            </div>
          </div>
          <div className="row" style={{ margin: "80px 0 50px 0" }}>
            <div className="col-xl-6" ref={scrollRef}>
              <Bar data={chartData} options={options} height={200} />
            </div>
            <div className="col-xl-6">
              <Bar data={energyData} options={optionsEnergy} height={200} />
            </div>
          </div>
          <div className="row">
            <table
              style={{
                marginBottom: "50px",
                fontFamily: "Arial",
                fontSize: "18px",
                marginLeft: "35px",
              }}
            >
              <tr>
                <th style={{ width: "90px" }}>Fibre</th>
                <th style={{ width: "90px" }}>Fibre</th>
                <th style={{ width: "90px" }}>Resin</th>
                <th style={{ width: "90px" }}>Fibre%</th>
                <th style={{ width: "210px" }}>Process</th>
                <th style={{ width: "90px" }}>Color</th>
              </tr>
              {submittedOptions.map((option, i) => (
                <tr key={i}>
                  <td>Option {i + 1}</td>
                  <td>{option.fibre}</td>
                  <td>{option.resin}</td>
                  <td>{option.RW} %</td>
                  <td>{option.process}</td>
                  <td>
                    <div
                      style={{
                        width: "50px",
                        height: "20px",
                        backgroundColor: colors[i],
                      }}
                    ></div>
                  </td>
                </tr>
              ))}
            </table>
          </div>
        </div>
      </MainWrapper>

      <FooterContainer>
        <p>
          The emission data mentioned above was derived from the database
          utilized by Far UK, and the calculations pertain to a specific concept
          design of a car model.
        </p>
      </FooterContainer>
    </>
  );
}
